body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
    background-color: #EDD4B4;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    overflow: hidden;
    text-align: center;
}

.home .header {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    letter-spacing: 5px;
    font-family: 'Gaegu';
    font-size: 500%;
}

.home h2 {
    color: black;
}

.home a {
    text-decoration: none;
}

.home .selection-container {
    display: flex;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    max-width: 1500px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 auto;
    font-size: 150%;

    font-family: 'Gaegu';
}

.home .selection-component {
    min-width: 75px;
}

.home .selection-container img {
    width: 100%;
}

@media screen and (max-width: 1200px) {
    .home .selection-container {
        width: 60%;
    }
}

@media screen and (max-width: 1000px) {
    .home .selection-container {
        width: 70%;
        font-size: 100%;
    }
}


@media screen and (max-width: 500px) {

    .home .selection-container {
        width: 80%;
        font-size: 60%;
    }

}

.hoveractivity {
	 font-family: Montserrat, sans-serif;
	 font-size: 28px;
	 background-color: #EDD4B4;
	 width: 100vw;
	 height: 100vh;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
   overflow: hidden;
}

.hoveractivity .btn-challenge {
	 color: #ffefe0;
	 font-family: inherit;
	 font-size: inherit;
	 font-weight: bold;
	 background-color: #ebbb8e;
	 width: 195px;
	 height: 75px;
	 padding: 0;
	 border: 0;
	 margin: 0;
	 outline: 0;
	 flex-grow: 0;
	 flex-shrink: 0;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
	 border-radius: 12px;
	 position: relative;
	 transition: background-color 180ms ease-out;
}
.hoveractivity .btn-challenge > small {
	 color: #ffefe0;
	 font-size: 0.6em;
	 font-weight: normal;
}
.hoveractivity .btn-challenge:hover {
	 background-color: #cfa682;
}
.hoveractivity .btn-challenge:active, .btn-challenge:hover:active {
	 background-color: #cfa682;
}
.hoveractivity .hover-area {
	 width: 195px;
	 height: 75px;
	 display: grid;
	 grid-template-columns: repeat(13, 15px);
	 grid-template-rows: repeat(5, 15px);
	 position: absolute;
	 top: 0;
	 left: 0;
	 perspective: 500px;
}
 .hoveractivity .hover-area .hover-col {
	 z-index: 1;
}
 .hover-area .hover-col:nth-child(1):hover ~ .icon-home {
